/* TodoList.css */

/* General styles */

.todo-list {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  display: flex;
  height: 80vh;
  border: 1px solid rgb(0, 0, 0);
  padding: 20px;
  flex-direction: column;
  align-items: center;
  font-size: 20px;
  color: rgb(5, 5, 5);
  background-color: #57d4df;
  width: 40%;
  overflow:hidden; /* Added overflow property */
}

.add-todo {
  display: flex;
  margin-bottom: 20px;
}

.add-todo input {
  flex-grow: 1;
  padding: 15px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
  white-space: nowrap; /* Added white-space property */
  overflow: hidden; /* Added overflow property */
  text-overflow: ellipsis; /* Added text-overflow property */
}

.add-todo button {
  margin-left: 10px;
  padding: 5px 25px;
  background-color: #00ffae;
  color: #000000;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 20px;
  font-weight: bold;
}

ul {
  list-style: none;
  padding: 0;
}

.todo-item {
  position: relative;
  border-radius: 20px;
  margin: 10px 0;
  background-color: #dde4ea;
  padding: 10px 20px;
  display: flex;
  width: 450px;
  align-items: center;
  word-break: break-word; /* Added word-break property */
}

.todo-item input {
  padding: 10px 20px;
  font-size: 20px;
  flex-grow: 1; /* Added flex-grow property */
}

.todo-item span {
  flex-grow: 1;
  margin-right: 10px;
  word-break: break-word; /* Added word-break property */
}

.todo-item-buttons {
  display: flex;
}

.todo-item-buttons button {
  margin-left: 5px;
  padding: 5px 10px;
  background-color: #dc3545;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.todo-item-buttons .edit-button {
  background-color: #007bff;
}

/* Responsive styles */

@media only screen and (max-width: 600px) {
  .container {
    width: 80%;
  }

  .todo-item {
    width: 90%;
    flex-wrap: wrap; /* Added flex-wrap property */
  }

  .todo-item input {
    width: 100%;
    margin-top: 10px; 
    margin-bottom: 10px; 

    /* Added margin-top property */
  }

  .todo-item-buttons {
    flex-wrap: wrap; /* Added flex-wrap property */
  }

  .add-todo {
    flex-direction: column;
    align-items: stretch;
  }

  .add-todo input {
    margin-bottom: 10px;
  }
}
